import React, { Component } from 'react';
import { connect } from 'react-redux';
import {
	AuthorWrapper,
	Avatar,
	AuthorInfo,
} from '../style';

class Author extends Component {
	render() {
		return(
				<AuthorWrapper>
					<AuthorInfo>
						<Avatar alt={this.props.author} src={"https://s.gravatar.com/avatar/9c7b2927850924199e3f8c1fffcb5672?s=80"}/>
						<div className="divide"></div>
						<div className="info">
							<div>作者 <a className="name">Cyan</a></div>
							<div>文章数 <a className="name">15</a></div>
							<div>最后更新 <a className="name">5小时前</a></div>
						</div>
					</AuthorInfo>
				</AuthorWrapper>
		);
	}
}

const mapStateToProps = (state) => ({

});

const mapDispatchToProps = (dispatch) => ({
	
});

export default connect(mapStateToProps, mapDispatchToProps)( Author );