import React, { Component } from 'react';
import { connect } from 'react-redux';
import {
	ArticleWrapper,
	AuthorInLine,
	Avatar,
	ArticleBody
} from '../style';
import CodeBlock from "./CodeBlock";
import ReactMarkdown from 'react-markdown';
import { MDWrapper } from './style';
import { Skeleton } from 'antd';
import { actionCreators } from '../store';


class Article extends Component {

	componentDidMount() {
		this.props.initLoading();
	}

	render() {
		if(this.props.loading) {
			return (
				<Skeleton paragraph={{ rows: 10 }} loading={this.props.loading} active></Skeleton>
			)
		} else return (
			<ArticleWrapper>
				<MDWrapper>
					<ReactMarkdown
						source={this.props.content}
						escapeHtml={false} 
						renderers={{
							code: CodeBlock
						}}
					/>
				</MDWrapper>
			</ArticleWrapper>
		);
	}
}

const mapStateToProps = (state) => ({
	title: state.getIn(["detail","title"]),
	content: state.getIn(["detail","content"]),
	author: state.getIn(["detail","author"]),	
	avatar: state.getIn(["detail","avatar"]),
	date: state.getIn(["detail","date"]),
	time: state.getIn(["detail","time"]),
	loading: state.getIn(["detail","loading"])
});

const mapDispatchToProps = (dispatch) => ({
	initLoading() {
		dispatch(actionCreators.setLoadingAction(true));
	}
});

export default connect(mapStateToProps, mapDispatchToProps)( Article );