import React, { PureComponent } from 'react';
import { actionCreators } from './store';
import Article from './components/Article';
import Author from './components/Author';
import Recommend from './components/Recommend';
import { connect } from 'react-redux';
import {
	Body,
	DetailWrapper,
	DetailBody,
	DetailSide,
} from './style';

/*Flex布局 */
class Detail extends PureComponent {
	componentDidMount() {
    window.scrollTo(0,0);
    window.addEventListener("mousewheel", this.props.handleDetailScroll);
		this.props.getInDetail(true); 
		const id = this.props.match.params.id;
		this.props.detailDataCatch(id);

  }
  componentWillUnmount() {
    this.props.getInDetail(false); 
  }
	render() {
		return (
			<Body>
				<DetailWrapper>
					<DetailBody>
						{/* 文章 */}
						<Article/>
					</DetailBody>
					<DetailSide>
						{/* 作者信息 */}
						<Author/>
					</DetailSide>
				</DetailWrapper>
			</Body>
		)
	}
}

const mapDispatchToProps = (dispatch) => ({
  detailDataCatch(id) {
		dispatch(actionCreators.getDetailDataAction(id));
  },
	/* 判断当前页面是否为文章页面， */
  getInDetail(istrue) {
    if(istrue) {
      dispatch(actionCreators.getInDetailAction(true));
    }else{
      dispatch(actionCreators.getInDetailAction(false));
    }
  }
});

export default connect(null, mapDispatchToProps)( Detail );
