import styled from 'styled-components';

export const Body = styled.div`
    width: 100%;
    display: flex;
    justify-content: center;
    margin-top: 15px;
    margin-bottom: 20px;
    min-height: 100vh;
`;

export const DetailWrapper = styled.div`
    width: 1000px;
    display: flex;
    justify-content: space-between;
    min-height: 500px;
`;

export const DetailBody = styled.div`
    width: 810px;
    display: flex;
    border-radius: 10px;
    background: #fff;
    flex-direction: column;
    padding: 10px;
`;

export const DetailSide = styled.div`
    width: 180px;
    display: flex;
    flex-direction: column;
`;

export const ArticleWrapper = styled.div`
    width: 100%;
    padding: 24px;
    margin-bottom: 20px;
    background: #fff;
    .title {
        color: #404040;
        font-size: 30px;
        font-weight: 700;
        line-height: 42px;
        margin-bottom: 15px;
    }
`;

