export const AuthorWrapper = styled.div`
    position: sticky;
    top: 71px;
    width: 180px;
    height: 260px;
    margin-bottom: 10px;
    padding: 20px;
    background: #fff;
    display: flex;
    border-radius: 10px;
    flex-direction: column;
    justify-content: flex-start;
    .divide {
        height: 15px;
        width: 228px;
        margin-top: 10px;
        margin-bottom: 30px;
        border-top: 2px solid rgb(238,238,238);
    }
`;

export const AuthorInfo = styled.div`
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin-bottom: 16px;
    .info {
        font-size: 16px;
        height: 45px;
        margin-left: 8px;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
    }
    .name {
        margin-bottom: 3px;
        font-size: 14px;
        color: #404040;
        cursor: pointer;
    }
    div {
        font-size: 14px;
        color: rgb(150,150,150);
        margin-bottom: 15px;
    }
`;

export const Avatar = styled.img`
    width: 100px;
    height: 100px;
    border-radius: 50px;
    border: 2px solid rgb(238,238,238);
`;

export const ArticleBody = styled.div`
    font-size: 16px;
    line-height: 30px;
    color: rgb(64,64,64);
    min-height: 70vh;
    img {
        margin: 20px auto;
        max-width: 100%;
        max-height: 100%;
    }
`;

export const RecommendWrapper = styled.div`
    background: #fff;
    width: 180px;
    padding: 16px;
    margin-bottom: 10px;
    .title {
        margin-bottom: 16px;
        border-left: 4px solid 	#008B8B;
        font-size: 16px;
        padding-left: 6px;
    }
`;

export const RecItem = styled.div`
    width: 180px;
    margin-bottom: 12px;
    .head {
        color: rgb(64,64,64);
        font-size: 14px;
        line-height: 22px;
        margin-bottom: 4px;
    }
    .total {
        color: rgb(150,150,150);
        font-size: 12px;
        line-height: 17px;
    }
`;